import React, { Component } from 'react';
import { IndexLink } from 'react-router'
// import logo from './logo.svg';
import './App.css';



class Sub extends Component {
  componentWillMount() {
    console.log('zi组件挂载之前');
  }
  componentDidMount() {
    console.log('zi组件挂载结束');
  }

  componentWillReceiveProps(nextProps) {
    console.log('9 子组件将要接收到新属性')
  }
  shouldComponentUpdate(nextProps, nextState) {
    console.log('10, 子组件是否需要更新');
    if (nextProps.number < 5) return true;
    return false
  }
  componentWillUpdate() {
    console.log('11 子组件将要更新')
  }

  componentDidMount() {
    console.log('13 子组件更新完毕');
  }
  componentWillUnmount() {
    console.log('14、子组件将卸载');
  }

  render() {
    console.log('12 子组件将要更新')
    return (
      <div>
        <p>{this.props.number}</p>
      </div>
    );
  }
}


class App extends Component {
  constructor(props) {
    super(props);
    this.state = { number: 0 }
  }
  componentWillMount() {
    console.log('3父组件挂载之前');
  }
  componentDidMount() {
    console.log('5 父组件挂载结束');
  }

  shouldComponentUpdate(newProps, newState) {
    console.log('6 父组件是否需要更新');
    if (newState.number < 15) {
      return true;

    }
    return false;
  }
  componentWillUpdate() {
    console.log('7、父组件将要更新');

  }
  componentDidUpdate() {
    console.log('8父组件更新完成')
  }
  handleClick = () => {
    this.setState({
      number: this.state.number + 1
    })
  };
  render() {
    console.log('4、render(父组件挂载)');
    return (
      <div className="App">
        <ul>
          <li><IndexLink to="/about">About</IndexLink ></li>
          <li><IndexLink to="/inbox">Inbox</IndexLink ></li>
        </ul>
        <div>
          <p>{this.state.number}</p>

          <button onClick={this.handleClick}>+</button>
          {this.state.number < 10 ? <Sub number={this.state.number} /> : null}
        </div>
        {this.props.children}
      </div>
    );
  }
}

export default App;
